import { createApp } from "vue"
import App from "./App.vue"
import "./registerServiceWorker"
import router from "./router"
import store from "./store"
import aframe from "aframe"
import axios from "axios"
import VueAxios from "vue-axios"
// import aframeWebSurface from "../node_modules/aframe-websurfaces"
import aframeHtmlembed from "aframe-htmlembed-component"
// import "../src/assets/js/mobile-look-control.js"
import aframeGui from "aframe-gui"
// import aframeExtras from "aframe-extras"
// import eventSet from "aframe-event-set-component"
import aframeLayoutComponent from "aframe-layout-component"
// import aframeRandomizer from "aframe-randomizer-components"
import aframeLookAt from "aframe-look-at-component"
import aframeEnvironment from "aframe-environment-component"
// import aframePortals from "aframe-portals"
// import BootstrapVue from "bootstrap-vue";
import "bootstrap/dist/css/bootstrap.css"
import "../public/fonts/materialdesignicons/css/materialdesignicons.css"
import "../public/scss/style.scss"
// import "bootstrap-vue/dist/bootstrap-vue.css";
import Camera from "./components/common/Camera"
import MenuBar from "./components/common/MenuBar"
import MultiScreen from "./components/common/MultiScreen"
import Screen2 from "./components/common/Screen2"
import CommonAssets from "./components/common/CommonAssets"

const app = createApp(App)

// Custom global components
app.component("Camera", Camera)
app.component("MenuBar", MenuBar)
app.component("MultiScreen", MultiScreen)
app.component("Screen2", Screen2)
app.component("CommonAssets", CommonAssets)

// Mixins
app.mixin({
  methods: {
    webUrl: function (u) {
      let url
      if (u !== null && u !== undefined) {
        url = window.location.origin+"/"+ u
      } else {
        url = window.location.origin
      }
      return url
    },
  },
})
app.use(aframe)
// app.component(Camera)
// app.use(aframeWebSurface)
// app.use(aframeExtras)
// app.use(eventSet)
app.use(aframeGui)
app.use(aframeHtmlembed)
app.use(aframeLayoutComponent)

app.use(aframeLookAt)
app.use(aframeEnvironment)

// app.use(BootstrapVue)
// Optionally install the BootstrapVue icon components plugin
// app.use(IconsPlugin)
app.use(store)
app.use(router)
app.use(VueAxios, axios)
app.mount("#app")
