<template>
  <router-view />
</template>
<script>
export default {
  provide () {
    // let vm = this
    return {
      isMobile: this.checkForWindowsResolution()
    }
  },
  data () {
    return { isMobile: false }
  },
  created () {
    window.addEventListener("resize", this.checkForWindowsResolution)

  },
  methods: {
    checkForWindowsResolution () {
      if (window.matchMedia("(min-width: 1024px)").matches) {
        /* The viewport is less than, or equal to, 700 pixels wide */
        this.isMobile = false
        // console.log('test desktop', this.isMobile, this)
      } else {
        /* The viewport is greater than 700 pixels wide */
        this.isMobile = true
        // console.log('test mobile', this.isMobile)
      }
      return this.isMobile
    }
  }
  
}
</script>
