<template>
  <a-entity>
    <a-entity id="stage" position="0 5 -5">
      <!-- <a-box
        material="color: #284a9e"
        geometry="depth: 2; height: .15; width: 2"
        position="0 -4.5 0"
        scale
      ></a-box>-->
      <a-cylinder
        material="color: #284a9e"
        geometry="radius: 3; height: 0.15; "
        position="0 -4.81638 0"
        scale
      ></a-cylinder>
    </a-entity>
    <a-cylinder
      id="windowCylinder"
      color="#fff"
      scale="-1 1 1"
      position="0 8 0"
      height="16"
      open-eded="true"
      side="double"
      repeat=".5 .5"
      offset="1 1"
      geometry="radius:20;openEnded:true;buffer: false;"
      material="opacity:0.2;color:#000;"
      segments-radial="180"
    >
      <!-- <a-link position="0 -5 2"  link="href: portal1.html; title: My Homepage; image: #redwoods"></a-link> -->
      <!-- <a-entity position="0 -5 2" link="href: index.html; title: My Homepage; image: #redwoods"></a-entity> -->

      <!-- <a-link class="screen" to="/portal1"  image="#redwoods"></a-link> -->

      <a-circle
        material="opacity:.3color: #000; side: double"
        radius="20"
        geometry="primitive: circle;  radius: 20.05; segments: 180"
        position="0 8.1 0"
        rotation="90 0 0"
      ></a-circle>
      <a-circle
        material="opacity:.1 color: #000; side: double"
        radius="20"
        geometry="primitive: circle;  radius: 20.05; segments: 180"
        position="0 -7.9 0"
        rotation="90 0 0"
      ></a-circle>
      <a-ring
        material="opacity: 1; color: cyan; side: double"
        radius="20"
        geometry="primitive: ring; thetaLength: 360.01; thetaStart: -0.19; radiusInner: 18.82; radiusOuter: 20.05; segmentsTheta: 180"
        position="0 8 0"
        rotation="90 0 0"
      ></a-ring>
      <a-ring
        material="opacity: 1; color: cyan; side: double"
        radius="20"
        geometry="primitive: ring; thetaLength: 360.01; thetaStart: -0.19; radiusInner: 18.82; radiusOuter: 20.05; segmentsTheta: 180"
        position="0 -7.8 0"
        rotation="90 0 0"
      ></a-ring>
      <a-entity light="angle: 59.71; intensity: 1; type: ambient"></a-entity>
    </a-cylinder>
    <a-entity id="targetLook" position="0 4.2 -.1"></a-entity>
    <a-entity
      id="screenWrapper"
      position="0 5 -12"
      look-at="#targetLook"
      :layout="'type: box; columns:' + calcColumn + '; marginRow: 3.5; marginColumn: 5;align:center;'"
    >
      <!-- <a-entity layout="type: circle; radius:10: plane:xz;" position="0 0 -5"> -->
      <!-- <a-entity  position="0 0 -5"> -->
      <a-entity
        id="img1"
        class="screenImage clickable"
        @click="setPostion"
        mixin="image"
        :material="'src:'+ i"
        look-at="#targetLook"
        v-for="(i,idx) in multiScreenData"
        :key="idx"
      >
        <a-entity
          class="btnWrapper clickable"
          visible="false"
          htmlembed="ppu:90"
          style="width:40px"
          position="0 -1.5 .03"
        >
          <button
            @click="sendtoPrevious"
            position="0 2 0"
            class="btn btn-danger clickable btn-sm btn-close"
          >
            <span class="mdi mdi-close"></span>
          </button>
        </a-entity>
      </a-entity>
    </a-entity>
  </a-entity>
</template>
<script>
export default {
   props: {
    multiScreenData: Array,
  },
  data () {
    return {
      currentEl: undefined,
      currentElPos: undefined,
      previousEl: undefined,
      previousElPos: undefined,
      previousElRot: undefined,
      initialPos: undefined,
      initialRot: undefined,
      stagePos: undefined,
      // calcColumn:3
      // isActive: false
    }
  },
  computed: {
    calcColumn(){
      return this.multiScreenData.length/2
    }

  },
  methods: {

    setPostion (e) {
      // alert()
      let stage = document.getElementById("stage")
      // var worldPos = new window.THREE.Vector3()
      this.currentEl = e.target
      // let btnClose = e.target.querySelector('.btn-close')
      // let this.currentElPos = currentEl.object3D.getWorldPosition(worldPos)
      this.currentElPos = this.currentEl.getAttribute('position')
      this.currentElRot = this.currentEl.getAttribute('rotation')
      //  this.currentElPos = worldPos.setFromMatrixPosition(this.currentEl.object3D.matrixWorld);
      //  let this.currentElPos = worldPos.setFromMatrixPosition(currentEl.object3D.matrixWorld);
      this.initialPos = this.currentElPos
      this.initialRot = this.currentElRot
      // let stagePos = stage.object3D.getWorldPosition(worldPos)
      this.stagePos = stage.getAttribute('position')
      // this.stagePos = worldPos.setFromMatrixPosition(stage.object3D.matrixWorld)
      // let stagePos = worldPos.setFromMatrixPosition(stage.object3D.matrixWorld);
      this.currentElPos = this.stagePos
      this.previousElPos = JSON.parse(window.localStorage.getItem("previousElPos"))
      this.previousElRot = JSON.parse(window.localStorage.getItem("previousElRot"))

      // this.previousElPos = JSON.parse(test)
      // console.log("worldPos", worldPos)
      // console.log("currentEl, this.currentElPos", this.currentEl, this.currentElPos)
      // console.log("stagePos", this.stagePos)
      // if (this.previousEl !== undefined) {
      //   console.log("previousElPos before", this.previousElPos)
      //   console.log("previousEl", this.previousEl)
      // }


      // console.log('check',this.currentEl.hasAttribute('data-active'))
      if (this.currentEl.hasAttribute('data-active')) {
        //  this.currentEl.removeAttribute('animation__1') 
        //  this.currentEl.removeAttribute('animation__2') 
        this.currentEl.querySelector('.btn-close').addEventListener('click', this.sendtoPrevious(this.currentEl, this.previousElPos))
        // this.sendtoPrevious(this.currentEl, this.previousElPos)
        //  this.currentEl.setAttribute('animation__3',
        //   { 'property': 'scale', 'to': { x: 5, y: 5, z: 5 } })
        // console.log('previousState', this.currentEl,this.initialPos, this.previousElPos)
      }
      else if (this.previousEl !== undefined && this.previousEl !== this.currentEl) {
        if (this.previousEl.hasAttribute('not-placed')) {
          this.sendtoPrevious(this.previousEl, this.previousElPos)
        }
        this.setActiveScreen(e.target, this.currentElPos)

      }
      else {
        this.setActiveScreen(e.target, this.currentElPos)
      }
      this.previousEl = this.currentEl
      // this.previousElPos = this.initialPos
      localStorage.setItem("previousElPos", JSON.stringify(this.initialPos))
      localStorage.setItem("previousElRot", JSON.stringify(this.initialRot))
      // console.log("this.currentElPos after", this.currentElPos)
      // console.log("previousElPos after", this.previousElPos)
      // //   console.log("test pos",previousEl, previousElPos)
    },
    sendtoPrevious (el, elPos) {
      let screenWrapper = document.getElementById('screenWrapper')
      let targetLook = document.getElementById('targetLook')
      el.querySelector('.btnWrapper').setAttribute('visible', 'false')
      el.removeAttribute('look-at')
      el.setAttribute('rotation', this.previousElRot)
      el.removeAttribute('data-active')
      el.setAttribute('placed', 'true')
      el.removeAttribute('not-placed')
      screenWrapper.setAttribute('animation__2',
        { 'property': 'position', 'to': { x: 0, y: 5, z: -12 }, 'dur': 500 })
      targetLook.setAttribute('animation__2', 
        { 'property': 'position', 'to': { x: 0, y: 4.2, z: -.1 }, 'dur': 500 })
      el.setAttribute('animation__1',
        { 'property': 'position', 'to': { x: elPos.x, y: elPos.y, z: (elPos.z * -1.6) }, 'dur': 500 })
      el.setAttribute('animation__2',
        { 'property': 'scale', 'to': { x: 1, y: 1, z: 1 }, 'dur': 500 })
    },
    setActiveScreen (el, elPos) {
      let screenWrapper = document.getElementById('screenWrapper')
      let targetLook = document.getElementById('targetLook')
      el.querySelector('.btnWrapper').setAttribute('visible', 'true')
      el.setAttribute('look-at', '[camera]')
      screenWrapper.setAttribute('animation__2',
        { 'property': 'position', 'to': { x: 0, y: 11, z: -12 }, 'dur': 500 })
      targetLook.setAttribute('animation__2', 
        { 'property': 'position', 'to': { x: 0, y: 8.5, z: -.1 }, 'dur': 500 })
      el.setAttribute('animation__1',
        { 'property': 'position', 'to': { x: elPos.x, y: (elPos.y - 11.1), z: (elPos.z * -1.6) }, 'dur': 500 })
      el.setAttribute('animation__2',
        { 'property': 'scale', 'to': { x: 1.8, y: 1.8, z: 1.8 }, 'dur': 500 })
      el.setAttribute('data-active', 'true')
      el.removeAttribute('placed')
      el.setAttribute('not-placed', 'true')
    }
  },
}
</script>
<style lang="scss">
</style>